import React from 'react';
const component = React.createClass({
    mixins: [],
    propTypes: {
    },
    getDefaultProps() {
        return {
            jsxprefixCls:"uxcore-select"
        };
    },
    getInitialState() {
        return {
            defaultValue: this.props.defaultValue
        };
    },
    componentDidMount: function() {
       console.info(this.getValue());
    },

    componentDidUpdate: function() {
       
    },
    getValue: function() {
       return this.refs.el.getDOMNode().value;
    },

    render() {
        console.info(this.props.jsxdata);
        var child=this.props.children, options;
        if(child) {
            options=child;
        }else {
            // options
             options = this.props.jsxdata.map(function(opt, i) {
                var selected="";
                if(opt.selected) {
                    selected="selected";
                }
                return ( <option key={'option-' + i} value={opt.value} selected={selected}>{opt.label}</option> );
            });
            //if (this.props.prependEmptyOption || this.props.firstOption) {
                options.unshift( <option key="option-blank" value="">{'Select...'}</option> );
            //}
        }
        return (
          <select className="uxcore-select" ref="el"  disabled={this.props.jsxdisabled} onChange={this.props.onChange} onBlur={this.props.onBlur}>
            {options}
          </select>
        );
    }
});
export default component;



